import {FC} from 'react'
import bestMoviesImg from '../../../images/250bestmovies.png'
import popularFilmsImg from '../../../images/popularfilms.png'
import movies from '../../../images/movies.png'
import FilmsComponents from '../../FilmsComponents'

const Films: FC  = () => {
  return (
    <div className='items-center'>
        <FilmsComponents img={movies} title=" " subtitle=' ' link='/moviesite/films/allfilms' />
        <FilmsComponents img={bestMoviesImg} title=" " subtitle=' ' link="/moviesite/films/bestmovies"/>
        <FilmsComponents img={popularFilmsImg} title=" " subtitle=' ' link="/moviesite/films/popular"/>
    </div>
  )
}

export default  Films